//import the types of actions supported
import {
  GET_ALL_EVENT,
  GET_FREE_SLOTS,
  CREATE_EVENT,
  CREATE_EVENT_ERROR,
  CLEAR_REDUCER,
} from "../actions/Types";
import { IEvents } from "../components/Events/eventsInterface";
import { IFreeSlot } from "../components/Events/eventsInterface";

//Actions type to accept either START_Events or STOP_Events type
export type Actions =
  | {
      type: typeof GET_FREE_SLOTS;
      payload: IFreeSlot[];
    }
  | {
      type: typeof GET_ALL_EVENT;
      payload: IEvents[];
    }
  | {
      type: typeof CREATE_EVENT;
      payload: any;
    }
  | {
      type: typeof CREATE_EVENT_ERROR;
      payload: any;
    }
  | {
      type: typeof CLEAR_REDUCER;
    };

interface EventsInterface {
  freeSlots: IFreeSlot[];
  allEvents: IEvents[];
  eventSucessMessage: any;
  eventErrorMessage: any;
}

//State type for defining the state of the reducer
export type State = EventsInterface;

//Initial state of the reducer of type State
export const initialState: State = {
  freeSlots: [],
  allEvents: [],
  eventSucessMessage: "",
  eventErrorMessage: "",
};

//Events reducer which takes a state and an action param
export const Events = (state: State = initialState, action: Actions) => {
  //switch between action.type
  switch (action.type) {
    case GET_FREE_SLOTS:
      return {
        ...state,
        freeSlots: action.payload,
      };
    case GET_ALL_EVENT:
      return {
        ...state,
        allEvents: action.payload,
      };
    case CREATE_EVENT:
      return {
        ...state,
        eventSucessMessage: action.payload,
      };
    case CREATE_EVENT_ERROR:
      return {
        ...state,
        eventErrorMessage: action.payload,
      };
    case CLEAR_REDUCER:
      return {
        freeSlots: [],
        allEvents: [],
        eventSucessMessage: "",
        eventErrorMessage: "",
      };

    //return state as it is if action is not of any of the aforementioned types
    default:
      return state;
  }
};
