import React, { useState } from "react";
import "../styles/Events/get-all-events.css";
import { DatePicker } from "../Common/DatePicker";
import { screenType } from ".";
import { Button } from "../Common/Button";
import Dropdown from "react-dropdown";
import "react-dropdown/style.css";
import { IEvents } from "./eventsInterface";
import { Loading } from "../Common/Loading";

const options = ["Asia/Kolkata", "Asia/Qatar", "Europe/Berlin"];

interface Props {
  toggleScreen: any;
  isLoading: boolean;
  getEvents: Function;
  allEvents: IEvents[];
}

export const GetAllEvents: React.FC<Props> = ({
  toggleScreen,
  isLoading,
  getEvents,
  allEvents,
}) => {
  const [startDate, setStartDate] = useState<Date>();
  const [endDate, setEndDate] = useState<Date>();
  const [timeZone, setTimeZone] = useState("Asia/Kolkata");
  const [error, setError] = useState("");

  const onBackClick = () => {
    toggleScreen(screenType.createEvent);
  };

  const onClickButton = () => {
    if (!startDate && !endDate) {
      setError("Please select Date Range");
    } else {
      setError("");
      getEvents(startDate, endDate);
    }
  };

  return (
    <div className="get-event-main-container">
      {isLoading && <Loading />}
      <p className="back-button" onClick={() => onBackClick()}>
        Back to Home
      </p>
      <div className="range-select-container">
        <div className="start-date-container">
          <DatePicker
            label="Start Date"
            handleChange={(e) => setStartDate(new Date(e.target.value))}
            id="start-duration-filter"
          />
        </div>

        <div className="end-date-container">
          <DatePicker
            label="End Date"
            handleChange={(e) => setEndDate(new Date(e.target.value))}
            id="end-duration-filter"
          />
        </div>

        <div className="timezone-dropdown-container">
          <p>Select Timezone</p>
          <Dropdown
            options={options}
            onChange={(elem) => setTimeZone(elem.value)}
            value={timeZone}
            placeholder="Select an option"
          />
        </div>

        <Button
          value={"Get All Events"}
          id={"allEvents"}
          handleClick={onClickButton}
        />
      </div>
      {error && <p className="error-msg">{error}</p>}
      <div className="event-list-main-container">
        <div className="event-list-container">
          {allEvents.map((event: IEvents, index) => (
            <div className="single-event-container" key={index}>
              <p>Date : {event.date}</p>
              <p>Time : {event.formattedTime}</p>
              <p>Duration : {event.duration + " mins"}</p>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};
