import React, { useState } from "react";
import { Button } from "../Common/Button";
import { TextField } from "../Common/TextField";
import Calendar from "react-calendar";
import "react-calendar/dist/Calendar.css";
import Dropdown from "react-dropdown";
import "react-dropdown/style.css";
import "../styles/Events/create-event.css";
import { screenType } from ".";
import { Loading } from "../Common/Loading";
import { IFreeSlot } from "./eventsInterface";
import eventImg from "../../assets/images/events.svg";

const options = ["Asia/Kolkata", "Asia/Qatar", "Europe/Berlin"];

interface Props {
  toggleScreen: any;
  isLoading: boolean;
  getFreeSlots: Function;
  createNewEvent: Function;
  freeSlots: IFreeSlot[];
}

export const CreateEvent: React.FC<Props> = ({
  toggleScreen,
  isLoading,
  getFreeSlots,
  createNewEvent,
  freeSlots,
}) => {
  const [selectedDate, setSelectedDate] = useState(new Date());
  const [duration, setDuration] = useState(30);
  const [timeZone, setTimeZone] = useState("Asia/Kolkata");
  const [selectedSlot, setSelectedSlot] = useState("");

  const getMaxDate = () => {
    const todayDate = new Date();
    let maxDate = todayDate.setMonth(todayDate.getMonth() + 12);
    return new Date(maxDate);
  };

  const onDateChange = (e: any) => {
    setSelectedDate(e);
  };

  const onClickSlot = (slot: IFreeSlot) => {
    setSelectedSlot(slot.formattedTime);
    createNewEvent(slot.date, duration, selectedDate, timeZone);
  };

  const viewEvents = () => {
    toggleScreen(screenType.getAllEvent);
  };

  return (
    <div>
      <p className="view-button" onClick={() => viewEvents()}>
        View Events
      </p>
      {isLoading && <Loading />}
      <div className="create-event-main-container">
        <div className="create-event-left-container">
          <div className="create-event-header">
            <img src={eventImg} alt="Event" />
            <p>Create an Event</p>
          </div>
          <Calendar
            onChange={onDateChange}
            value={selectedDate}
            defaultActiveStartDate={new Date()}
            defaultValue={"month"}
            minDate={new Date()}
            maxDate={getMaxDate()}
            tileDisabled={({ date }) => [0, 6].includes(date.getDay())}
          />

          <div className="row-container">
            <div className="duration-textfield-container">
              <TextField
                value={duration}
                name="duration"
                placeholder={"Enter Duration"}
                label={"Enter Duration"}
                id={"duration"}
                type="number"
                handleChange={(e) => setDuration(parseInt(e.target.value))}
              />
            </div>

            <div className="timezone-dropdown-container">
              <p>Select Timezone</p>
              <Dropdown
                options={options}
                onChange={(elem) => setTimeZone(elem.value)}
                value={timeZone}
                placeholder="Select an option"
              />
            </div>
          </div>
          <Button
            value={"Get Free Slots"}
            id={"getFreeSlots"}
            handleClick={() => getFreeSlots(selectedDate, timeZone)}
          />
        </div>

        <div className="create-event-right-container">
          <p className="select-slot-header">
            {freeSlots.length > 0
              ? "Select a slot to create an event"
              : "Select a Date to get Free Slots"}
          </p>
          <div className="free-slots-container">
            {freeSlots.map((slot: IFreeSlot, index) => (
              <p
                key={index}
                onClick={() => onClickSlot(slot)}
                style={
                  selectedSlot === slot.formattedTime
                    ? { background: "var(--primaryBlue)", color: "#fff" }
                    : {}
                }
              >
                {slot.formattedTime}
              </p>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};
