import React, { useContext, useState } from "react";
import { CreateEvent } from "./CreateEvent";
import { GetAllEvents } from "./GetAllEvents";
import { EventsContext } from "../../context/Events";
import { LoadingContext } from "../../context/Loading";
import { clearReducer, getAllEvents, getFreeSlots } from "../../actions/events";
import { getFormattedDate } from "../../helpers/getFormattedDate";
import { createEvent } from "../../actions/events";

export enum screenType {
  createEvent,
  getAllEvent,
}

export const Events: React.FC = () => {
  const { state: eventState, dispatch: eventDispatch } =
    useContext(EventsContext);
  const { dispatch: loadingDispatch } = useContext(LoadingContext);
  const [currentScreen, setCurrentScreen] = useState(screenType.createEvent);
  const [isLoading, setIsLoading] = useState(false);

  const toggleScreen = async (screenName: screenType) => {
    setCurrentScreen(screenName);
    await clearReducer()(eventDispatch);
  };

  const fetchFreeSlots = async (selectedDate: Date, timeZone: string) => {
    setIsLoading(true);
    await getFreeSlots(getFormattedDate(selectedDate), timeZone)(
      eventDispatch,
      loadingDispatch
    );
    setIsLoading(false);
  };

  const fetchAllEvents = async (startDate: Date, endDate: Date) => {
    setIsLoading(true);
    await getAllEvents(getFormattedDate(startDate), getFormattedDate(endDate))(
      eventDispatch,
      loadingDispatch
    );
    setIsLoading(false);
  };

  const createNewEvent = async (
    dateTime: Date,
    duration: number,
    freeSlotDate: Date,
    timeZone: string
  ) => {
    setIsLoading(true);
    await createEvent(dateTime, duration)(eventDispatch, loadingDispatch);
    await getFreeSlots(getFormattedDate(freeSlotDate), timeZone)(
      eventDispatch,
      loadingDispatch
    );
    setIsLoading(false);
  };

  return (
    <div>
      {currentScreen === screenType.createEvent ? (
        <CreateEvent
          toggleScreen={toggleScreen}
          isLoading={isLoading}
          getFreeSlots={fetchFreeSlots}
          createNewEvent={createNewEvent}
          freeSlots={eventState.freeSlots}
        />
      ) : (
        <GetAllEvents
          toggleScreen={toggleScreen}
          isLoading={false}
          getEvents={fetchAllEvents}
          allEvents={eventState.allEvents}
        />
      )}
    </div>
  );
};
