/* eslint-disable no-unused-expressions */

import React from "react";
import "../styles/text-field.css";

//Props interface. Defines all the required props by the component
interface Props {
  value: string | number | readonly string[] | undefined; //value of the input field
  placeholder: string; //placeholder text
  textFieldStyle?: object; //additional styles object
  name?: string;
  handleChange?: (event: React.ChangeEvent<HTMLInputElement>) => void; //onChange event handler
  label: string; //Label Text
  type?: string; //Type of input field. By default, text
  id: string; //Id of the input field. Would be postfixed with _element
  disabled?: boolean; //if props say to disable the field then disable
  error?: string; //Error message for a text field (optional)
  width?: string; //Width props to provide width to the container
  minChar?: number | 20; //Props for min char of input default -20
  maxChar?: number | 20; //Props for max char of input default -20
  underlineTextField?: boolean;
  showCrossIcon?: boolean;
  textFieldContainerStyle?: object;
  handleRemove?: any;
  pattern?: string;
}

//Common TextField Component
export const TextField: React.FC<Props> = ({
  label,
  placeholder,
  name,
  handleChange,
  type,
  id,
  textFieldStyle,
  value,
  disabled,
  error,
  width,
  minChar,
  maxChar,
  underlineTextField,
  textFieldContainerStyle,
  pattern,
}) => {
  return (
    <div className="main-container" style={width ? { width: width } : {}}>
      <div className="label-div">
        <label className={"label"} htmlFor={id + "_element"}>
          {label}
        </label>
      </div>
      <div
        style={textFieldContainerStyle ? textFieldContainerStyle : {}}
        // based on fields like disabled error and warning change the class names to present different
        //  styles to the user
        className={`${"text-field-container"} ${error ? " error-input" : ""}`}
      >
        <input
          value={value}
          id={id + "_element"}
          placeholder={placeholder}
          type={type ? type : "text"}
          pattern={pattern}
          className={"input-text-field"}
          name={name}
          style={textFieldStyle ? textFieldStyle : {}}
          onChange={handleChange}
          disabled={disabled ? true : false}
          minLength={minChar} //minimum number of inputs
          maxLength={maxChar} //maximum number of inputs
        />
      </div>
      {/* Display the error message if any */}
      {error ? (
        <span
          className={"input-error-message"}
          id={"error-message" + id}
          key={error}
        >
          {error}
        </span>
      ) : null}
    </div>
  );
};
