import { startLoading, stopLoading } from "./loading";
import { Actions as LoadingActions } from "../reducers/loading";
import { Actions } from "../reducers/events"; //import user actions interface
import axios from "axios";
import { API_URL } from "./serverConnection";
import {
  CLEAR_REDUCER,
  CREATE_EVENT,
  CREATE_EVENT_ERROR,
  GET_ALL_EVENT,
  GET_FREE_SLOTS,
} from "./Types";

export const getFreeSlots =
  (date: any, timezone: string) =>
  async (
    dispatch: React.Dispatch<Actions>,
    loadingDispatch: React.Dispatch<LoadingActions>
  ) => {
    try {
      //dispatch start loading
      startLoading(loadingDispatch);
      //fetch the results from the sign in API
      const result = await axios.post(`${API_URL}/events/free-slots`, {
        date,
        timezone,
      });
      dispatch({
        type: GET_FREE_SLOTS,
        payload: result.data.data,
      });
      //Stop loading
      stopLoading(loadingDispatch);
    } catch (err) {
      stopLoading(loadingDispatch);
    }
  };

