export const createEvent =
  (dateTime: Date, duration: any) =>
  async (
    dispatch: React.Dispatch<Actions>,
    loadingDispatch: React.Dispatch<LoadingActions>
  ) => {
    try {
      //dispatch start loading
      startLoading(loadingDispatch);
      //fetch the results from the sign in API
      const result = await axios.post(`${API_URL}/events/`, {
        dateTime: dateTime.toString(),
        duration,
      });
      dispatch({
        type: CREATE_EVENT,
        payload: result.data.data,
      });
      //Stop loading
      stopLoading(loadingDispatch);
    } catch (err: any) {
      dispatch({
        type: CREATE_EVENT_ERROR,
        payload: err.data.message,
      });
      stopLoading(loadingDispatch);
    }
  };

export const clearReducer = () => async (dispatch: React.Dispatch<Actions>) => {
  dispatch({
    type: CLEAR_REDUCER,
  });
};
